<template>
    <div class="register">
        <a-row>
            <a-col :span="12">
                <img src="https://huobi-1253283450.file.myqcloud.com/bit/banner/6e926582-efe8-4494-ae87-9b04f9b28f74.png" alt="img">
            </a-col>
            <a-col :span="12">

                <a-form @submit="handleSubmit" :form="form" :label-col="{ span: 24 }" :wrapper-col="{ span: 12 }" class="t-left">
                    <h3 class="mt-60">Register Personal Account</h3>
                    <a-form-item label="Email">
                        <a-input :disabled="loading" v-decorator="['Email', { rules: [
                        { type: 'email', message: 'The input is not valid E-mail!' },
                        { required: true, message: 'Please input your Email!' }
                        ] }]" />
                    </a-form-item>

                    <a-form-item label="Password(Must contain 6 or more characters)">
                        <a-input :disabled="loading" @blur="handleConfirmBlur" v-decorator="['Password', { rules: [
                        { required: true, message: 'Please enter your password as required!' },
                        { validator: validateToNextPassword }
                        ] }]" type="password" />
                    </a-form-item>

                    <a-form-item label="Confirm Password">
                        <a-input :disabled="loading" v-decorator="['ConfirmPassword', { rules: [
                        { required: true, message: 'Please enter the password again!' },
                        { validator: compareToFirstPassword }
                        ] }]" type="password" />
                    </a-form-item>

                    <a-form-item label="Referral Code(Optional)">
                        <a-input :disabled="loading" v-decorator="['ReferralCode', { rules: [{ required: false, message: 'code 4 length!' }] }]" />
                    </a-form-item>

                    <a-form-item>
                        <a-checkbox v-decorator="[
                        'IsRead',
                        {
                            valuePropName: 'checked',
                            initialValue: true,
                            rules: [{ required: true, message: 'Please check, I have carefully read the user agreement!' }]
                        },
                        ]">
                            I've read it carefully.
                        </a-checkbox>
                        <router-link to="/help">《User Agreement》</router-link>
                    </a-form-item>

                    <a-form-item>
                        <a-row>
                            <a-button type="primary" :loading="loading" block html-type="submit">Register</a-button>
                        </a-row>
                        <a-row>
                            <router-link to="/login">Login</router-link>
                        </a-row>
                    </a-form-item>
                </a-form>

            </a-col>
        </a-row>
        <div style="margin-top: 100px;"></div>
    </div>
</template>


<script>
export default {
    name: "Register",
    components: {},
    data() {
        return {
            loading: false,
            confirmDirty: false,
            autoCompleteResult: [],
        };
    },
    beforeCreate() {
        this.form = this.$form.createForm(this, { name: "register" });
    },
    methods: {
        handleSubmit(e) {
            e.preventDefault();
            this.form.validateFieldsAndScroll((err, values) => {
                if (err) {
                    console.error(err);
                    return;
                }
                if (!values.IsRead) {
                    this.$message.warning(
                        "Please check, I have carefully read the user agreement!"
                    );
                    return;
                }

                this.loading = true;
                $.ajax({
                    url: `${this.g.apiUrl}/api/register`,
                    data: values,
                    type: "post",
                    error: (XMLHttpRequest, textStatus, errorThrown) => {
                        this.loading = false;
                        alert(XMLHttpRequest.responseJSON.message);
                    },
                    success: (res) => {
                        this.loading = false;
                        if (res.flag != "ok") {
                            alert(res.flag);
                            return;
                        }
                        alert("Register successfully, go to login now!");
                        this.$router.push("/login");
                    },
                });

                // $.post(`${this.g.apiUrl}/api/register`, values, (res) => {
                //     this.loading = false;
                //     if (res.flag != "ok") {
                //         alert(res.flag);
                //         return;
                //     }
                //     alert("Register successfully, go to login now!");
                //     this.$router.push("/login");
                // });
            });
        },
        handleConfirmBlur(e) {
            const value = e.target.value;
            this.confirmDirty = this.confirmDirty || !!value;
        },
        compareToFirstPassword(rule, value, callback) {
            const form = this.form;
            if (value && value !== form.getFieldValue("Password")) {
                callback("Two passwords that you enter is inconsistent!");
            } else {
                callback();
            }
        },
        validateToNextPassword(rule, value, callback) {
            const form = this.form;
            if (value && value.length < 6) {
                callback("Must contain 6 or more characters.");
            }
            if (value && this.confirmDirty) {
                form.validateFields(["confirm"], { force: true });
            }
            callback();
        },
        init() {},
    },
    mounted() {
        this.init();
    },
};
</script>



